import * as React from 'react';
import { connect } from 'react-redux';
import { withRouter, RouteComponentProps } from 'react-router-dom';
import SidebarComponent from '../../layouts/Sidebar';
import { Segment, Header } from 'semantic-ui-react';
import SeriesDetailHoc from '@music/hocs/series/DetailHoc';
import { IState, ISeries, IMix } from '@music/interfaces';
import EntityCollection from '@music/extensions/entity-collection';
import { has } from '@music/utils';
import MixBox from '@music/components/MixBox';
import { isEqual } from 'lodash';
import gaService from '@music/_services/gaService';
import MixEntity from '@music/entities/mix';

interface ISeriesState {
  currentSeries: ISeries;
  mixes?: IMix[];
}

type ISeriesDetailProps = RouteComponentProps & {
  series: EntityCollection;
}

class SeriesDetail extends React.Component<ISeriesDetailProps, any> {
  public state: Readonly<ISeriesState> = {
    currentSeries: {} as ISeries,
  };

  constructor(props: ISeriesDetailProps) {
    super(props);
  }

  public componentDidUpdate(prevProps: ISeriesDetailProps) {
    if (! isEqual(this.props.match.params, prevProps.match.params)) {
      this.getData((name) => gaService.pageView(`Series Detail: ${name}`));
    }
  }

  public componentDidMount() {
    this.getData((name) => gaService.pageView(`Series Detail: ${name}`));
  }

  public render() {
    return (
      <>
        <SidebarComponent>
          <Segment style={{height: 'auto'}} className="toptracks-container">
            <Header as='h1'>{this.state.currentSeries.name}</Header>
            {this.state.mixes &&
              <MixBox data={[...this.state.mixes]} />
            }
          </Segment>
        </SidebarComponent>
      </>
    );
  }

  private getData(cb: (title: string) => any = () => null) {
    if (has('name', this.props.match.params)) {
      const { name } = this.props.match.params as any;
      const currentSeries = this.props.series.find(name, 'slug') as ISeries;

      (new MixEntity).getMixesBySeries(currentSeries.id, 0, 20).then((mixes: IMix[]) => {
        this.setState({ mixes, currentSeries }, () => cb(currentSeries.name));
      }).catch(console.error);
    }
  }
}

// const mapDispatchToProps = (dispatch: CallableFunction) => {
//   return {
//     closeModal: (domain: string) => dispatch(closeModalAction(domain)),
//     saveFormData: (data: IAdvertisementFormData) => dispatch(saveAdFormData(data)),
//     setNextStep: (step: string) => dispatch(setNextStepAction(step)),
//   }
// };

const mapStateToProps = (state: IState) => {
  return {
    series: new EntityCollection(state.series.data),
  }
};

const SeriesDetailPage = connect(mapStateToProps)(SeriesDetail);

export default withRouter(SeriesDetailHoc(SeriesDetailPage));



